body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee;
  background-size: 100px 100px, 100px 100px, 20px 20px, 20px 20px;
  background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  background-image: linear-gradient(rgba(199,199,199,.3) 2px, transparent 2px),
                    linear-gradient(90deg, rgba(199,199,199,.3) 2px, transparent 2px),
                    linear-gradient(rgba(199,199,199,.3) 1px, transparent 1px),
                    linear-gradient(90deg, rgba(199,199,199,.3) 1px, transparent 1px);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  margin: 0 auto;
}

.main {
  padding: 5px auto;
}

.product-add-form {
  padding: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
}

.input-label {
  display: block;
  margin-left: 3px;
}

.input-field {
  display: block;
  border: 1px solid gray;
  border-radius: 5px;
  height: 22px;
}

.input-field:focus {
  border: 1px solid black;
}

.button-wrapper {
  display: inline;
}

.button {
  display: block;
  border: 1px solid black;
  border-radius: 5px;
  box-sizing: content-box;
  height: 24px;
  padding: 0 10px;
}

.card {
  box-sizing: border-box;
  background-color: #CBE8CA;
  color: #77328F;
  border-radius: 5px;
  padding: 5px 15px 10px;
  width: 320px;
  margin: 5px auto;
}

.discount-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  margin: 5px;
}

.table-wrapper {
  background-color: #CBE8CA;
  color: black;
  margin: 10px auto;
  overflow-y:auto;
  width:100%;
}

.message {
  font-style: italic;
}

.table {
  margin: 5px auto;
  border: 1px solid gray;
  border-spacing: 0;
  border-collapse: collapse;
}

.table-header {
  background-color: #A6D7E8;
  font-weight: bold;
}

.oddrow {
  background-color: #dedede;
}

.evenrow {
  background-color: #efefef;
}

.table-cell {
  border: 1px solid gray;
  padding: 1px 10px;
}


